import { Component, OnInit , Input } from '@angular/core';
import { WpServiceService} from '../wp-service/wp-service.service';
import { TagIdNamePipe } from '../tag-id-name.pipe';

@Component({
  selector: 'card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.scss'],
  providers: [WpServiceService]
})
export class CardComponent implements OnInit {

      @Input('catId') cat;
      @Input('postCount') catCount;

      public count = 6;
      public postList;
      public tags;
      public tagArray = {};
      public paginator = true;

      constructor( private wpService : WpServiceService ) { }

      ngOnInit() {

        // Cheak pagination
        if( this.catCount < 6){
          this.paginator = false;
        }

        // Load Post
        this.fetchPost(this.cat, this.count)

      }


      // Paginator Click
      loadPost(){
        // Cheak pagination
        if( (this.catCount - this.count) < 6){
          this.count = this.catCount;
          this.paginator = false;
        }
        else{
          this.count = this.count + 6;
        }

        // Load Post
        this.fetchPost(this.cat, this.count)

      }



      fetchPost(cat , count) {
        this.wpService.getTags().subscribe(
          res => {
            res.forEach((item) =>{
              this.tagArray[item.id] = item.name;
            })

            // Fetch post data
            this.wpService.getData(cat, count).subscribe(
              res => {
                this.postList = res ;
                // console.log(this.postList)
              }
            )
          }
        )
  }
}



